<template>
  <div class="hello">
    <button @click="mqtt('1')">开</button>
    <button @click="mqtt('0')">关</button>
  </div>
</template>

<script>
/* eslint-disable */
import mqtt from 'mqtt'
var client
const options = {
  port: 28083,
  connectTimeout: 4000,
  clientId: 'mqtt_' + Math.random().toString(16).substring(2, 8)
}
client = mqtt.connect('ws://cn-yw-plc-4.openfrp.top/mqtt', options)
export default {
  created() {
    this.mqttMsg()
  },
  methods: {
    mqttMsg() {
      client.on('connect', (e) => {
        console.log('连接成功')
        client.subscribe('/test/helloworld', { qos: 0 }, (error) => {
          console.log(error)
        })
      }),
        client.on('message', (topic, message) => {
          //console.log('收到来自', topic, '的消息', message.toString())
          if (message.toString() == 'hi') {
            client.publish('/test/abc', 'hello', { qos: 0, rein: false }, (error) => {
              console.log('error', error)
            })
          }
        })

      client.on('reconnect', (error) => {
        console.log('正在重连', error)
      }),
        client.on('error', (error) => {
          console.log('连接失败', error)
        })
    },
    mqtt(str) {
      console.log(str);
      client.publish('inTopic', str, { qos: 0, rein: false }, (error) => {
        console.log('error', error)
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
button {
  text-decoration: none;
  position: relative;
  border: none;
  font-size: 14px;
  font-family: inherit;
  color: #fff;
  width: 9em;
  height: 3em;
  line-height: 2em;
  text-align: center;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
  background-size: 300%;
  border-radius: 30px;
  z-index: 1;
  margin: 20px;
}

button:hover {
  animation: ani 8s linear infinite;
  border: none;
}

@keyframes ani {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 400%;
  }
}

button:before {
  content: '';
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
  background: linear-gradient(90deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
  background-size: 400%;
  border-radius: 35px;
  transition: 1s;
}

button:hover::before {
  filter: blur(20px);
}

button:active {
  background: linear-gradient(32deg, #03a9f4, #f441a5, #ffeb3b, #03a9f4);
}
</style>



